.news-detail
{
  width: 890px;
  margin: 0 auto;
  margin-bottom: 150px;
  @include l
  {
    width: 100%;
    margin-bottom: 100px;
  }
}
.news-detail__top
{

}
.news-detail__main-image
{
  width: 100%;
  height: 620px;
  overflow: hidden;
  border-radius: 5px;
  margin-bottom: 40px;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include l
  {
    height: 476px;
  }
  @include xm
  {
    height: 240px;
    margin-bottom: 20px;
  }
}
.news-detail__desc
{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  @include xm
  {
    margin-bottom: 15px;
  }
}
.news-detail__category
{
  @include s1-d;
  line-height: 20px;
  margin-right: 20px;
  padding-right: 20px;
  border-right: 1px solid #000;
  @include l
  {
    font-size: 18px;
    line-height: 20px;
  }
  @include xm
  {
    font-size: 14px;
    line-height: 90%;
    margin-right: 10px;
    padding-right: 10px;
  }
}
.news-detail__date
{
  @include t1-d;
  @include l
  {
    font-size: 18px;
    line-height: 150%;
  }
  @include xm
  {
    font-size: 14px;
    line-height: 18px;
  }
}
.news-detail__name
{
  margin-bottom: 30px;
  h1
  {
    @include h3-d;
  }
  @include xm
  {
    margin-bottom: 20px;
    h1
    {
      @include h2-m
    }
  }
}
.news-detail__content
{
  p
  {
    @include t1-d;
  }
  @include l
  {
    p
    {
      font-weight: 400;
      font-size: 18px;
      line-height: 150%;
    }
  }
  @include xm
  {
    p
    {
      @include t1-m
    }
  }
}
.news-detail__image-with-text
{
  margin: 40px 0;
  @include xm
  {
    margin: 20px 0;
  }
}
.image-with-text__image
{
  width: 100%;
  height: 440px;
  border-radius: 5px;
  overflow: hidden;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include l
  {
    height: 340px;
  }
  @include xm
  {
    height: 180px;
  }
}
.image-with-text__text
{
  margin-top: 20px;
  color: #C6C6C6;
  @include s2-d;
  @include l
  {
    font-size: 16px;
    line-height: 21px;
  }
  @include xm
  {
    @include s1-m;
  }
}
.news-detail__solo-image
{
  width: 100%;
  height: 390px;
  border-radius: 5px;
  overflow: hidden;
  margin: 40px 0;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include l
  {
    height: 300px;
  }
  @include xm
  {
    margin: 20px 0;
    height: 150px;
  }
}
.news-detail__content-bottom
{
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 100px;
}
.news-detail__source
{
  height: 50px;
}
.news-detail__source-header
{
  @include t1-d;
  @include xm
  {
    @include t1-m;
  }
}
.news-detail__source-value
{
  @include t1-d;
  @include xm
  {
    @include t1-m;
  }
}
.news-detail__share
{
  padding: 10px 15px;
  background-color: #A43131;
  border-radius: 5px;
  color: #fff;
  display: flex;
  align-items: center;
  gap: 5px;
  transition: all .3s ease;
  &:hover
  {
    transform: translateY(-3px);
  }
  @include t2-d;
}



.news-detail__images-with-swiper
{
  width: 100%;
  margin: 40px 0;
  position: relative;
  @include l
  {
    margin: 20px 0;
  }
}
.news-detail__swiper
{
  overflow: hidden;
  padding-bottom: 46px;
  @include l
  {
    padding-bottom: 34px;
  }
}
.news-detail__swiper-image
{
  width: 100%;
  height: 440px;
  overflow: hidden;
  border-radius: 5px;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include l
  {
    height: 440px;
  }
  @include xm
  {
    height: 170px;
  }
}
.news-detail__swiper-pagination
{
  //display: none;
    display: block;
    height: 10px;
    .swiper-pagination-bullet
    {
      width: 10px;
      height: 10px;
      background: rgba(113, 121, 126, 0.5);
      margin: 0 9px !important;
      &-active
      {
        background: #000;
      }
      @include l
      {
        width: 6px;
        height: 6px;
        margin: 0 5px !important;
      }
    }
    @include l
    {
      height: 6px;
    }
}
.news-detail__swiper-buttons
{

  position: absolute;
  width: 60px;
  height: 60px;
  top: 50%;
  transform: translate(0,-50%);
  z-index: 9;
  cursor: pointer;
  color: rgb(113, 121, 126);
  transition: all .4s ease;
  &:hover
  {
    color: #aaa;
  }
  .icon
  {
    width: 100%;
    height: 100%;
  }
  @include l
  {
    display: none;
  }
}
.news-detail__swiper-buttons-next
{
  left: -100px;
}
.news-detail__swiper-buttons-prev
{
  right: -100px;
  .icon
  {
    transform: rotateZ(180deg);
  }
}
.news-detail__comments
{

}
.news-detail__comments-header
{
  margin-bottom: 30px;
  h2
  {
    @include h3-d;
    @include xm
    {
      @include h3-m;
    }
  }
}
.news-detail__comments-list
{

}
.news-detail__comment
{

}
.news-detail__comment-top
{
  display: flex;
  align-items: center;
}
.news-detail__comment-image
{
  width: 100px;
  height: 100px;
  background-color: #D9D9D9;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 20px;
  @include xm
  {
    width: 60px;
    height: 60px;
  }
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.news-detail__comment-top-text
{
  display: flex;
  align-items: center;
}
.news-detail__comment-name
{
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 90%;
  margin-right: 20px;
  padding-right: 20px;
  border-right: 1px solid #000;
  @include xm
  {
    @include h4-m;
    margin-right: 10px;
    padding-right: 10px;
  }
}
.news-detail__comment-date
{
  @include t1-d;
  color: #C6C6C6;
  @include xm
  {
    @include t1-m;
  }
}
.news-detail__comment-content
{
  margin-top: 30px;
  margin-bottom: 40px;
  p
  {
    @include t1-d;
  }
  @include xm
  {
    margin-top: 15px;
    margin-bottom: 20px;
    p
    {
      @include t1-m;
    }
  }
}
.news-detail__comment-bottom
{
  ul
  {
    display: flex;
    li
    {
      margin-right: 20px;
      padding-right: 20px;
      border-right: 1px solid #C6C6C6;
      @include t3-d;
      color: #C6C6C6;
      &:last-child
      {
        margin-right: 0px;
        padding-right: 0px;
        border-right: none;
      }
      @include xm
      {
        margin-right: 10px;
        padding-right: 10px;
        @include t1-m;
      }
    }
  }
}
