.main-banner
{
  margin-bottom: 50px;
  @include l
  {
    margin-left: -20px;
    margin-right: -20px;
  }
  @include xm
  {
    margin-left: 0px;
    margin-right: 0px;
  }
}
.main-banner__swiper
{
  position: relative;
  border-radius: 5px;
  overflow: hidden;
  margin-top: -152px;
  @include l
  {
    margin-top: -142px;
  }
}
.main-banner__btns
{
  position: absolute;
  z-index: 99;
  bottom: 40px;
  right: 40px;
  display: flex;
  @include l
  {
    display: none;
  }
}
.main-banner__btn-left
{

}
.main-banner__btn
{
  color: #fff;
  cursor: pointer;
  transition: all .4s ease;
  .icon
  {
    width: 60px;
    height: 60px;
  }
  &:hover
  {
    color: #aaa;
  }
}
.main-banner__btn-right
{
  margin-left: 30px;
  .icon
  {
    transform: rotateZ(180deg);
  }
}
.index-page
{
  //margin-top: 90px;
  margin-top: 60px;
}
.banner
{
  display: block;
  width: 100%;
  height: 735px;
  position: relative;
}
.banner__image
{
  width: 100%;
  height: 100%;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
    @include xm
    {
        object-position: -250px top;
    }
  }
}
.banner__image-ton
{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.5;
}
.banner__text
{
  position: absolute;
  bottom: 40px;
  left: 40px;
  z-index: 2;
  color: #fff;
  @include l
  {
    left: 20px;
  }
  @include xm
  {
    bottom: 20px;
  }
}
.banner__text-top
{
  @include s1-d;
  margin-bottom: 15px;
  @include l
  {
    margin-bottom: 10px;
  }
  @include xm
  {
    @include s1-m
  }
}
.banner__text-head
{
  width: 60%;
  h1
  {
    @include h1-d
  }
  @include l
  {
    width: 500px;
    h1
    {
      @include h2-d
    }
  }
  @include xm
  {
    width: 287px;
    h1
    {
      @include h1-m
    }
  }
}
.banner__text-bot
{
  width: 540px;
  @include t1-d;
  margin-top: 20px;
  @include l
  {
    width: 500px;
    margin-top: 15px;
    @include t2-d
  }
  @include xm
  {
    width: 310px;
    margin-top: 10px;
    @include t1-m
  }
}
.uzor
{
  margin: 0 calc(50% - 50vw);
  height: 80px;
  background-image: url('../img/uzor.png');
	background-repeat: repeat;
  background-size: 412px 80px;
  @include l
  {
    display: none;
  }
}
.last-events
{
  margin: 100px 0;
  @include l
  {
    margin: 80px 0;
  }
  @include xm
  {
    margin: 60px 0;
  }
}
.last-events__header
{
  text-align: center;
  margin-bottom: 50px;
  h2
  {
    @include h1-d
  }
  @include l
  {
    text-align: left;
    margin-bottom: 40px;
    h2
    {
      @include h2-d
    }
  }
  @include xm
  {
    margin-bottom: 30px;
    h2
    {
      @include h1-m
    }
  }
}
.last-events__cards
{
  @include l
  {
    margin: 0 calc(50% - 50vw)
  }
}
.last-events__swiper
{
.swiper-slide
{
  width: 427px;
  margin-right: 40px;
  &:last-child
  {
    margin-right: 0
  }
  @include l
  {
    width: 350px;
    margin-right: 0;
    margin-left: 0px;
    padding-left: 20px;
    &:first-child
    {
      width: 370px;
      margin-left: 0px;
      padding-left: 40px;
    }
    &:last-child
    {
      margin-right: 40px;
    }
  }
  @include xm
  {
    &:first-child
    {
      margin-left: 0px;
      padding-left: 20px;
    }
    &:last-child
    {
      margin-right: 20px;
    }
  }
}
}
.event-card
{
  display: block;
  position: relative;
  border-radius: 2px;
  overflow: hidden;
  @include l
  {
    border-radius: 5px;
  }
  &:hover
  {
    .event-card__image
    {
      img
      {
        scale: 1.1;
      }
    }
  }
}
.event-card__image
{
  position: relative;
  width: 100%;

  height: 310px;
  overflow: hidden;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
      transition: all .3s ease;
  }
  @include l
  {
    height: 240px;
  }
}
.event-card__ton
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
}
.event-card__text
{
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: #fff;
  width: calc(100% - 40px);
  @include l
  {
    bottom: 17px;
    left: 17px;
    width: calc(100% - 34px);
  }
}
.event-card__category
{
  @include s3-d;
  margin-bottom: 10px;
  @include l
  {
    @include s1-m;
    margin-bottom: 10px;
  }
}
.event-card__name
{
  @include h5-d;
  @include l
  {
    @include h4-m
  }
}

.strategy
{
  margin-top: 50px;
  position: relative;
  height: 735px;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 100px;
  @include l
  {
    height: auto;
    margin: 0 calc(50% - 50vw);
    margin-bottom: 80px;
  }
}
.strategy-image
{
  position: relative;
  width: 100%;
  height: 100%;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include l
  {
    display: none;
  }
}
.strategy-image__ton
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: rgba(0, 0, 0, 0.5)
}
.strategy-name
{
  position: absolute;
  top: 50%;
  transform: translate(0,-50%);
  left: 107px;
  z-index: 2;
  color: #fff;
  width: 320px;
  h2
  {
    @include h1-d
  }
  @include xl
  {
    left: 50px;
    h2
    {
      @include h2-d;
    }
  }
  @include l
  {
    padding: 0 40px;
    position: static;
    width: 100%;
    color: #000;
    transform: none;
    margin-bottom: 40px;
  }
  @include xm
  {
    padding: 0 20px;
    margin-bottom: 30px;
    h2
    {
      @include h1-m
    }
  }
}
.strategy-list
{
  position: absolute;
  top: -208px;
  right: 0;
width: 640px;
height: 1160px;
@include l
{
  height: auto;
  position: static;
  width: 100%;
  color: #000;
  //margin: 0 calc(50% - 50vw);
}
}
.swiper-css-mode.strategy-list__swiper
{
  .swiper-wrapper
  {
    overflow-y: auto ;
    overflow-x: hidden;
    @include l
    {
      overflow-x: auto;
      overflow-y: hidden;
      margin-left: 40px;
    }
    @include xm
    {
      margin-left: 20px;
    }
  }
}
.strategy-list__swiper
{
  position: relative;
  height: 100%;
  .swiper-wrapper
  {
    @include l
    {
      margin-left: 40px;
    }
    @include xm
    {
      margin-left: 20px;
    }
  }
  .swiper-slide-active
  {
    .strategy-card__image
    {
      width: 100%;
      height: 100%;
      position: relative;
      opacity: 1;
      filter: blur(0px);
      transition: all .3s ease;
      &:hover
      {
        img
        {
          scale: 1.1;
        }
      }
      img
      {
        opacity: 1;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
  .swiper-slide-prev
  {
    @include l
    {
      //padding-right: 40px;
    }
  }
  .swiper-slide-next
  {
    .strategy-card__text
    {
      position: absolute;
      bottom: 160px;
      z-index: 2;
      @include l
      {
        top: auto;
        bottom: 20px;
        left: 20px;
      }
    }
  }
  .swiper-slide
  {
    @include l
    {
      width: auto;
      &:first-child
      {
        margin-left: 40px;
      }
      &:last-child
      {
        margin-right: 40px;
      }
    }
  }
}
.strategy-list__btns
{
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translate(0,-50%);
  z-index: 99;
  @include l
  {
    display: none;
  }
}
.strategy-list__btn
{
  width: 60px;
  height: 60px;
  color: #fff;
  transition: all .4s ease;
  cursor: pointer;
  .icon
  {
    width: 60px;
    height: 60px
  }
  &:hover
  {
    color: #aaa;
  }
}
.strategy-list__btn-next
{
  margin-bottom: 30px;
  .icon
  {
    transform: rotateZ(90deg);
  }
}
.strategy-list__btn-prev
{
  .icon
  {
    transform: rotateZ(-90deg);
  }
}
.strategy-card
{
  display: block;
  position: relative;
  width: 520px;
  height: 360px;
  border-radius: 5px;
  overflow: hidden;
  user-select: none;
  @include l
  {
    width: 330px;
    height: 240px;
  }
}
.strategy-card__image
{
  width: 100%;
  height: 100%;
  position: relative;
  opacity: 0.9;
  filter: blur(1px);
  background-color: #71797E;
  transition: all .4s ease;
  img
  {
    opacity: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all .4s ease;
  }
}
.strategy-card__ton
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
  z-index: 1;
}
.strategy-card__text
{
  position: absolute;
  bottom: 30px;
  left: 30px;
  z-index: 2;
  color: #fff;
  width: 450px;
  transition: all .5s ease;
  @include l
  {
    bottom: 20px;
    left: 20px;
    width: calc(100% - 40px);
  }
}
.strategy-card__category
{
  @include s3-d;
  margin-bottom: 10px;
  @include l
  {
    @include s1-m
  }
}
.strategy-card__name
{
  @include h4-d;
  @include l
  {
    @include h4-m
  }
}
.strategy-card__desc
{
  @include t2-d;
  margin-top: 10px;
  @include l
  {
    @include t1-m
  }
}
