footer
{
  background-color: #A43131;
  padding: 40px 0;
  user-select: none;
}
.footer
{

}
.footer__top
{
  display: flex;
  justify-content: space-between;
  @include xm
  {
    display: block;
  }
}
.footer__menu
{
  color: #fff;
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 23px;
  ul
  {
    display: flex;
    li
    {
      margin-right: 35px;
      transition: transform .3s ease;
      &:hover
      {
        transform: translateY(-3px);
      }
      &:last-child
      {
        margin-left: 0;
      }
    }
    @include l
    {
      display: block;
      li
      {
        margin-bottom: 24px;
        margin-right: 0;
        &:last-child
        {
          margin-bottom: 0
        }
      }
    }
    @include xm
    {
      li
      {
        text-align: center;
      }
    }
  }
}
.footer__contacts
{
  width: 310px;
  color: #fff;
  user-select: text;
  @include l
  {
    text-align: right;
  }
  @include xm
  {
    margin: 0 auto;
    margin-top: 90px;
    text-align: center;
  }
}
.footer__contacts-text
{
  font-family: 'PT Sans';
font-style: italic;
font-weight: 400;
font-size: 18px;
line-height: 150%;
margin-bottom: 5px;
}
.footer__contacts-tel
{
  font-family: 'PT Sans';
font-style: italic;
font-weight: 700;
font-size: 20px;
line-height: 150%;
}
.footer__bot
{
  display: flex;
  justify-content: space-between;
  margin-top: 80px;
  @include xm
  {
    display: block;
    margin-top: 60px;
  }
}
.footer__socials
{
  color: #fff;
  .icon
  {
    width: 32px;
    height: 32px;
  }
  ul
  {
    display: flex;
    @include xm
    {
      justify-content: center;
    }
    li
    {
      a
      {
        cursor: pointer;
        display: block;
        width: 32px;
        height: 32px;
        transition: all .2s ease;
        &:hover
        {
          scale: 1.1;
        }
      }
      margin-right: 20px;
      &:last-child
      {
        margin-right: 0;
      }
    }
  }
}
.footer__platform
{
  display: flex;
  color: #fff;
  @include xm
  {
    justify-content: center;
    margin-top: 40px;
  }
}
.platform__text
{
  margin-right: 8px;
  @include t3-d;
}
