.strategy-lists
{

}
.strategy-list__item
{
  margin-bottom: 50px;
  display: block;
}
.strategy-list__item-image
{
  width: 100%;
  height: 540px;
  overflow: hidden;
  border-radius: 5px;
  margin-bottom: 30px;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include l
  {
    height: 415px;
  }
  @include xm
  {
    border-radius: 0;
    height: 200px;
    margin-bottom: 15px;
  }
}
.strategy-list__item-text
{

}
.strategy-list__item-name
{
  h2
  {
      @include h3-d;
  }
  margin-bottom: 10px;
  @include xm
  {
    h2
    {
      @include h4-m
    }
  }
}
.strategy-list__item-desc
{
  @include t1-d;
  @include xm
  {
    @include t1-m
  }
}
