.top-tabs
{
  //margin-bottom: 80px;
  margin-bottom: 50px;
  ul
  {
    display: flex;
    align-items: flex-end;
  }
  @include l
  {
    margin-bottom: 40px;
    ul
    {
      justify-content: center;
    }
  }
  @include xm
  {
    margin-bottom: 50px;
  }
}

.top-tab__item
{
    margin-right: 50px;
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 42px;
    color: #C6C6C6;
    &:last-child
    {
      margin-right: 0;
    }
    @include xm
    {
      margin-right: 30px;
      font-size: 16px;
      line-height: 120%;
    }
  &_active
  {
    position: relative;
    margin-left: 22px;
    color: #000;
    @include h2-d;
    @include xm
    {
      margin-left: 11px;
      @include h3-s-m;
    }

    &::before
    {
      content: "";
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      left: -22px;
      width: 12px;
      height: 12px;
      background-color: #000;
      border-radius: 50%;
      @include xm
      {
        left: -11px;
        width: 6px;
        height: 6px;
      }
    }
    &-video
    {
      &::before
      {
        background-color: #FF0000;
      }
    }
  }
}
