.drop-down-menu
{
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  &_active
  {
    .drop-down-menu__inside
    {
      display: block;
    }
    .drop-down-menu__arrow
    {
      transform: rotateZ(180deg)
    }
  }
}
.drop-down-menu__header
{
  margin-bottom: 10px;
    @include h3-d;
    @include xm
    {
      margin-bottom: 12px;
    }
}
.drop-down-menu__outside
{
  display: flex;
  align-items: center;
}
.drop-down-menu__name
{
  @include t1-m
}
.drop-down-menu__arrow
{
  width: 24px;
  height: 24px;
}
.drop-down-menu__inside
{
  display: none;
  position: absolute;
  top: 90px;
  padding: 35px 30px;
  background-color: #A43131;
  color: #fff;
  z-index: 3;
}
.dd__arrow
{
  clip-path: polygon(50% 0%, 100% 100%, 0% 100%, 0% 100%);
  position: absolute;
  top: -10px;
  left: 50%;
  transform: translate(-50%,0);
  width: 30px;
  height: 10px;
  background-color: #A43131
}
.drop-down-menu__item
{
  margin-bottom: 30px;
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 100%;
  &:last-child
  {
    margin-bottom: 0
  }
  &_active
  {
    position: relative;
    margin-left: 15px;
    font-weight: 700;
    &::before
    {
      content: "";
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      left: -15px;
      width: 7px;
      height: 7px;
      background-color: #fff;
      border-radius: 50%;
    }
  }
}
