.search
{
  margin-bottom: 80px;
  @include l
  {
    margin-bottom: 50px;
  }
  @include xm
  {
    margin-bottom: 80px;
  }
}
.search__header
{
  margin-bottom: 50px;
  h1
  {
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 64px;
  }
  @include l
  {
    text-align: center;
    h1
    {
      line-height: 120%;
    }
  }
}
.search__list
{

}
.search__item
{
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  &:last-child
  {
    margin-bottom: 0;
  }
  @include l
  {
    margin-bottom: 20px;
  }
  @include xm
  {
    display: block;
    margin-bottom: 50px;
  }
}
.search__item-image
{
  width: 350px;
  height: 210px;
  border-radius: 5px;
  overflow: hidden;
  margin-right: 30px;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include l
  {
    width: 330px;
    height: 200px;
    margin-right: 15px;
  }
  @include xm
  {
    width: 100%;
    height: 210px;
    margin: 0;
    margin-bottom: 20px;
  }
}
.search__item-text
{
  flex: 1;
}
.search__item-name
{
  margin-bottom: 10px;
  h2
  {
    @include h4-d;
    @include l
    {
      @include h4-m
    }
    @include xm
    {
      @include h4-d
    }
  }
}
.search__item-desc
{
  p
  {
    @include t2-d;
    @include l
    {
      @include t1-m
    }
    @include xm
    {
      @include t2-d;
    }
  }
}
