.button
{
  cursor: pointer;
  user-select: none;
  padding: 14.5px 20px;
  color: #fff;
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 23px;
  background-color: #A43131;
  border-radius: 5px;
  transition: all .2s ease;
  &:hover
  {
    transform: translateY(-4px);
  }
  &_grey
  {
      background-color: #71797E;
      &:hover
      {
        background-color: #A43131;
      }
  }
}
