.survey
{
    width: 890px;
    margin: 0 auto;
    margin-bottom: 150px;
    @include l
    {
      width: 100%;
      margin-bottom: 100px;
    }
}
.survey__image
{
  width: 100%;
  height: 620px;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 45px;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.survey__header
{
  margin-bottom: 60px;
}
.survey__header-desc
{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.survey__category
{
  margin-right: 20px;
  padding-right: 20px;
  border-right: 1px solid #000;
  @include s1-d
}
.survey__date
{
  @include t1-d
}
.survey__header-name
{
  margin-bottom: 30px;
  h1
  {
    @include h3-d
  }
}
.survey__header-info
{
  @include t1-d
}
.survey__list
{

}
.survey__item
{
  margin-bottom: 60px;
  &:last-child
  {
    margin-bottom: 0;
  }
}
.survey__item-name
{
  margin-bottom: 30px;
  h2
  {
    @include h4-d;
  }
}
.survey__item-variants
{

}
.survey__variant
{
  margin-bottom: 30px;
  &:last-child
  {
    margin-bottom: 0
  }
}
.survey__count
{
  margin-top: 40px;
  @include s2-d;
}
.survey__btn
{
  margin-top: 40px;
}
