.topical
{
  margin-bottom: 150px;
  @include l
  {
    margin-bottom: 100px;
  }
}
.topical__header
{
  text-align: center;
  margin-bottom: 50px;
  h2
  {
    @include h1-d;
  }
  @include l
  {
    text-align: left;
    margin-bottom: 40px;
    h2
    {
      font-weight: 400;
      font-size: 48px;
      line-height: 120%;
    }
  }
  @include xm
  {
    margin-bottom: 30px;
    h2
    {
      font-weight: 400;
      font-size: 30px;
      line-height: 40px;
    }
  }
}
.topical-swiper
{
  .swiper-wrapper
  {
    .swiper-slide
    {
    width: auto;
    margin-right: 40px;
    &:last-child
    {
      margin-right: 0;
    }
    @include l
    {
      margin-right: 20px;
    }
    }
  }
}
.topical__card
{
  position: relative;
  width: 427px;
  height: 311px;
  border-radius: 2px;
  overflow: hidden;
  cursor: pointer;
  &:hover
  {
    .topical__card-image
    {
      img
      {
        scale: 1.1;
      }
    }

  }
  @include l
  {
    width: 330px;
    height: 240px;
    border-radius: 5px;
  }
}
.topical__card-image
{
  position: relative;
  width: 100%;
  height: 100%;

  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all .3s ease;
  }
}
.topical__card-ton
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
}
.topical__card-video
{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  transition: all .3s ease;
  @include l
  {
    display: none;
  }
  &-circle
  {
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    backdrop-filter: blur(14px);
  }
  &-play
  {
    width: 18px;
    height: 18px;
    position: absolute;
    top: 50%;
    left: calc(50% + 2px);
    transform: translate(-50%,-50%);
    .icon
    {
      width: 100%;
      height: 100%;
    }
  }
}
.topical__card-text
{
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: #fff;
  width: calc(100% - 40px);
}
.topical__card-text-top
{
  display: flex;
  align-items: center;
    margin-bottom: 10px;
}
.topical__card-category
{
  padding-right: 15px;
  margin-right: 15px;
  border-right: 1px solid #fff;
  @include s3-d;
  @include l
  {
    @include s1-m;
  }
}
.topical__card-date
{
  @include t3-d;
  @include l
  {
    @include t1-m;
  }
}
.topical__card-name
{
  h3
  {
    @include h5-d;
    @include l
    {
      @include h4-m;
    }
  }
}
