.bio
{

}
.bio__item
{
  display: flex;
  @include xm
  {
      flex-direction: column;
  }
}
.bio__side
{
  flex: 1;
  margin-right: 20px;
  @include l
  {
    display: none;
  }
  @include xm
  {
    display: block;
    margin-right: 0;
    margin-bottom: 35px;
  }
//  background-color: red;
}
.bio__side-item
{
  display: flex;
  align-items: center;
  position: sticky;
  top: 50%;
  transform: translate(0,-50%);
    margin-bottom: 60px;
    @include xm
    {
      top: 0;
      transform: none;
      position: relative;
      margin-bottom: 0;
    }
}
.bio__image
{
  width: 400px;
  height: 400px;
  background-color: grey;
  border-radius: 50%;
  margin-right: 30px;
  overflow: hidden;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include xl
  {
    width: 300px;
    height: 300px;
  }
  @include xm
  {
    display: none;
    margin-right: 0;
  }
}
.bio__name
{
  h2
  {
    font-family: 'PT Serif';
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 100%;
  }
}
.bio__content
{
  height: 2000px;
  flex: 1;
//  background-color: green;
}
.bio__content-section
{
  h2
  {
    @include h2-d;
    margin-bottom: 20px;
    @include xm
    {
      @include h3-m;
      margin-bottom: 15px;
    }
  }
  p
  {
    @include t2-d;
    margin-bottom: 20px;
    @include xm
    {
      @include t1-m;
      margin-bottom: 15px;
    }
  }
}
.bio__content-image-with-text
{
  margin: 40px 0;
  @include xm
  {
    margin: 15px 0;
  }
}
.bio__content-image
{
  width: 100%;
  height: 550px;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 20px;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include xm
  {
    margin-bottom: 15px;
    height: 350px;
  }
}
.bio__content-text
{
  color: #C6C6C6;
  @include s2-d;
  @include xm
  {
    @include s1-m
  }
}
