.left-menu
{
  // margin-top: 50px;
  position: sticky;
  top: 200px;
  max-width: 390px;
  width: 22vw;
  ul
  {
    margin-bottom: 40px;
  }
  @include l
  {
    display: none
  }
}
.left-menu__header
{
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 36px;

  margin-bottom: 20px;
}
.left-menu__item
{
  margin-bottom: 30px;
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 100%;
  transition: transform .3s ease;
  &:hover
  {
    transform: translateX(-3px);
  }
  a
  {

  }
  &:last-child
  {
    margin-bottom: 0
  }
  &_active
  {
    position: relative;
    margin-left: 15px;
    font-weight: 700;
    &::before
    {
      content: "";
      position: absolute;
      top: 50%;
      transform: translate(0, -50%);
      left: -15px;
      width: 7px;
      height: 7px;
      background-color: #000;
      border-radius: 50%;
    }
  }
}
.left-menu__dropdown
{
  display: none;
  @include l
  {
    display: flex;
    margin-bottom: 60px;
  }
  @include xm
  {
    margin-bottom: 50px;
  }
}
.left-menu__date-pickers
{
  display: flex;
}
.left-menu__date-picker
{
  position: relative;
  width: 105px;
  border-bottom: 2px solid #000;
  &:first-child
  {
    margin-right: 20px;
  }
  &_open
  {
    .date-picker__arrow
    {
      .icon
      {
                transform: rotate(180deg);
      }
    }
  }
input
{
  width: 100%;
  height: 40px;
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: flex-start;
  border: none;
  outline: none;
  opacity: 0;
  &:focus
  {
    &::-webkit-calendar-picker-indicator{

  }
  }
  &::-webkit-datetime-edit
  {
    display: flex;
    align-items: center;
  }
  &::-webkit-datetime-edit-fields-wrapper
  {
    height: fit-content;
  }
  &::-webkit-calendar-picker-indicator{
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 0;
    opacity: 0;
    z-index: 10;
    // background-image: url(../img/chevron-down.svg);
    // background-position: center right;
}
}
@include l
{
  border: none;
  width: 95px;
  input
  {
    height: 22px;
  }
}
}
.date-picker__arrow
{
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0,-50%);
  width: 24px;
  height: 24px;
  padding: 0;
  z-index: 0;
}
.date-picker__placeholder
{
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 23px;

  background-color: #fff;
  position: absolute;
  top: 50%;
  transform: translate(0,-50%);
  left: 0;
  z-index: 0;
  width: 85px;

}

.drop-down__flex
{
  display: none;
  justify-content: center;
  @include l
  {
    display: flex;
  }
  .drop-down-menu:first-child
  {
    margin-right: 100px;
    @include xm
    {
      margin-right: 30px;
    }
  }
}
