.news-outside__item
{
  display: block;
  position: relative;
  margin-bottom: 36px;
  @include xm
  {
    margin-bottom: 20px;
  }
  &:last-child
  {
    margin-bottom: 80px;
  }
  &:hover
  {
    .news-outside__item-image
    {
      img
      {
        transform: scale(1.1);
      }
    }
  }
}
.news-outside__item-image
{
  width: 100%;
  height: 620px;
  border-radius: 5px;
  overflow: hidden;
  @include l
  {
    height: 480px;
  }
  @include xm
  {
    height: 255px;
    border-radius: 2px;
  }
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all .4s ease
  }
}
.news-outside__item-ton
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
z-index: 1;
}
.news-outside__text
{
  position: absolute;
  width: calc(100% - 80px);
  color: #fff;
  bottom: 40px;
  left: 40px;
  z-index: 1;
  @include l
  {
    width: calc(100% - 60px);
    bottom: 30px;
    left: 30px;
  }
  @include xm
  {
    width: calc(100% - 40px);
    bottom: 20px;
    left: 20px;
  }
}
.news-outside__text-top
{
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  @include xm
  {
    margin-bottom: 10px;
  }
}
.news-outside__category
{
  padding-right: 20px;
  margin-right: 20px;
  border-right: 1px solid #fff;
  @include s2-d;
  @include xm
  {
    padding-right: 10px;
    margin-right: 10px;
    font-size: 14px;
    line-height: 150%;
  }
}
.news-outside__date
{
  @include t2-d;
  @include xm
  {
    font-size: 14px;
    line-height: 18px;
  }
}
.news-outside__text-bottom
{

}
.news-outside__name
{
  h2
  {
    @include h3-d;
    @include xm
    {
      font-size: 18px;
      line-height: 23px;
    }
  }
}
