header
{
  position: sticky;
  top: 20px;
  z-index: 100;
  transition: all .3s ease;
  padding: 0 40px;
  user-select: none;
  @include l
  {
    padding: 0 20px;
  }
  .header-wrap:last-child
  {
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 99;
    animation: .6s showHeader
  }
  @keyframes showHeader {
    0% {
      opacity: 0;
    }
    50% {
      opacity: 0;
    }
    50% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}
.header_opacity
{
  .header-wrap
  {
    &:last-child
    {
      border: none;
        background-color: inherit;
    }
    .header__logo-gerb
    {
      height: 100%;
      color: #fff;
      svg
      {
        fill: #fff;
      }
    }
    .header__logo-name
    {
      color: #fff;
    }
  }
  .header__logo-name-name
  {
    color: #fff;
  }
  .header__logo-name-post
  {
    color: #fff;
  }
  .header__menu
  {
    color: #fff;
  }
  .header__menu-active
  {
    &:after
    {
      background-color: #fff;
    }
  }
  .header__search-icon
  {
    color: #fff;
  }
  .header__search-pole
  {
    input
    {
      color: #fff;
    }
    border-bottom: 2px solid #fff;
    ::placeholder { /* Most modern browsers support this now. */
   color:    #fff;
}
  }
  .header__action-btn
  {
  background-color: #fff;
  color: #000;
  }


}
