.header
{
  margin: 0 auto;
  max-width: 1280px;
  padding: 20px 0;
}
.header-wrap
{
    border-radius: 5px;
  max-width: 1360px ;
//  padding: 0 40px;
  margin: 0 auto;
  margin-top: 10px;
  padding: 0 40px;
    border: 1px solid #F1F1F1;
    transition: all .3s ease;
  //border: 1px solid red;
  @include xl
  {
    margin-top: 0;
    padding: 0 20px;
  }
  @include xm
  {
    // /padding: 0 20px;
  }
  &_active
  {
    max-width: none;
    width: 100vw;
    padding: 0;
    .header__inside
    {
      padding: 0 80px;
    }
  }
}
.header-top
{
  height: 50px;
  background-color: #A43131;
  border: none;
  @include xl
  {
    display: none;
  }
}
.header-top__wrap
{
  max-width: 1280px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
}
.header-a11y
{
  display: flex;
  align-items: center;
}
.header-a11y__name
{
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  margin-right: 8px;
}
.header-a11y__icon
{
  width: 28px;
  height: 28px;
  .icon
  {
    width: 100%;
    height: 100%;
  }
}
.header-langs
{
  display: flex;
  font-family: 'PT Serif';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
}
.header-langs__item
{
  line-height: 16px;
  &:first-child
  {
    padding-right: 10px;
    margin-right: 10px;
    border-right: 1px solid #fff;
  }
  &_active
  {
    font-weight: 700;
  }
}

.header__inside
{
    position: relative;
  margin: 0 auto;
  // display: grid;
  // grid-template-columns: auto 1fr;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include l
  {
  }
  @include xm
  {

  }
}
.header__right
{
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  @include xl
  {
    display: none;
  }
}
.header__logo
{
  max-width: 390px;
  width: calc(22vw);
  height: 60px;
  display: flex;
  align-items: center;
  //background-color: red;
  //margin-right: 40px;
  a
  {
    display: flex;
    align-items: center;
  }
}
.header__logo-name
{
  margin-left: 8px;
}
.header__logo-name-name
{
  margin-bottom: 7px;
  color: #000;
}
.header__logo-name-post
{
  color: #000;
}
.header__logo-gerb
{
  height: 100%;
  color: #C82222;
  svg
  {
    fill: #234082
  }
}
.header__menu
{
  display: block;
  flex: 1;
  height: 50px;
  //background-color: green;
  margin-right: 106px;
  display: flex;
  align-items: center;
  transition: all .5s ease;
  animation: show .5s ;
  ul
  {
    width: 100%;
    display: flex;
    justify-content: space-between;
    li
    {
      position: relative;
      font-family: 'PT Sans';
font-style: normal;
font-weight: 400;
font-size: 17px;
line-height: 22px;
transition: transform .3s ease;
&:hover
{
  transform: translateY(-3px);
}
    }
  }
}

@keyframes show {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.header__mobile
{
  display: none;
  @include xl
  {
    display: block;
  }
  &_active
  {
    .header__mobile-btn-item-1
    {
      top: 9px;
      transform: rotate(45deg);
    }
    .header__mobile-btn-item-2
    {
      display: none;
    }
    .header__mobile-btn-item-3
    {
      bottom: 9px;
      transform: rotate(-45deg);
    }
    .header__mobile-menu
    {
      opacity: 1;
      height: calc(100vh - 100px);
    }
  }
}
.header__mobile-menu
{
  position: absolute;
  left: 0;
  margin: 0 -40px;
  margin-top: 30px;
  width: calc(100% + 80px);
    height: 0;
    overflow: hidden;
  background-color: #fff;
  transition: all .3s ease;
  opacity: 0;
}
.header__mobile-menu-wrap
{
  height: 100%;
  padding: 40px;
  overflow-y: auto;
}
.header__mobile-btn
{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: #f1f1f1;
  border-radius: 5px;
}
.header__mobile-inside
{
  width: 20px;
  height: 20px;
  position: relative;
}
.header__mobile-btn-item
{
  transition: all .3s ease;
  position: absolute;
  height: 2px;
  background-color: #000;
}
.header__mobile-btn-item-1
{
  top: 0;
  left: 0;
  width: 20px;
}
.header__mobile-btn-item-2
{
  top: 50%;
  transform: translate(0,-50%);
  left: 0;
  width: 15px;
}
.header__mobile-btn-item-3
{
  width: 20px;
  bottom: 0;
  left: 0;
}
.header__menu-active
{
  font-weight: 700 !important;
  &:after
  {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 0;
    height: 2px;
    width: 100%;
    background-color: #000;
  }
}
.header__search_active
{
  .header__menu
  {
    display: none;
  }
  .header__search-pole
  {
    width: 100%;
    //flex: 1;
    opacity: 1;
  }
  .header__search
  {
    flex: 1;
    //background-color: red
  }
  .header__menu
  {
    width: 0;
    opacity: 0;
  }
}
.header__search
{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  //background-color: red
}
.header__search-mobile
{
  display: flex;
