.district
{

}
.district__banner
{
  margin-top: -152px;
  margin-bottom: 100px;
  position: relative;
  width: 100%;
  height: 735px;
  @include xl
  {
    margin-top: -141px;
  }
  @include l
  {
    margin-top: -141px;
    margin-bottom: 60px;
    margin-right: -20px;
    margin-left: -20px;
    width: calc(100% + 40px);
  }
  @include xm
  {
    margin-right: 0;
    margin-left: 0;
    width: 100%;
  }
}
.district__banner-image
{
  border-radius: 5px;
  width: 100%;
  height: 100%;
  overflow: hidden;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.district__card
{
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translate(-50%,0);
  width: 577px;
  background: rgba(113, 121, 126, 0.5);
  backdrop-filter: blur(47px);
  border-radius: 5px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 40px 0;

  color: #fff;
  @include l
  {
    bottom: 50px;
  }
  @include xm
  {
    padding: 20px 0;
    width: calc(100% - 40px);
    bottom: 20px;
  }
}
.district__gerb
{
  width: 120px;
  height: 120px;
  margin-bottom: 20px;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include xm
  {
    width: 70px;
    height: 70px;
    margin-bottom: 15px;
  }
}
.district__name
{
  margin-bottom: 10px;
  h1
  {
    @include h2-d;
  }
  @include l
  {
    h1
    {
      font-size: 36px;
      line-height: 120%;
    }
  }
  @include xm
  {
    margin-bottom: 5px;
    h1
    {
      @include h3-s-m;
    }
  }
}
.district__name-sakha
{
  color: #C6C6C6;
  h2
  {
    font-family: 'PT Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
  }
  @include l
  {
    h2
    {
      font-size: 22px;
      line-height: 130%;
    }
  }
  @include xm
  {
    h2
    {
      @include t1-m;
    }
  }
}
.district__achievements
{
  width: calc(100% - 80px);
  margin: 0 auto;
  margin-bottom: 140px;
  @include l
  {
    width: 100%;
    margin-bottom: 80px;
  }
  @include xm
  {
    margin-bottom: 100px;
  }
}
.district__achievements-header
{

  text-align: center;
  margin-bottom: 40px;
  h2
  {
    @include h2-d;
  }
  @include l
  {
    text-align: left;
    h2
    {
      font-size: 48px;
      line-height: 120%;
    }
  }
  @include xm
  {
    margin-bottom: 30px;
    h2
    {
      @include h1-m;
    }
  }
}
.district__achievements-list
{
  .swiper-slide
  {
    width: auto;
  }
}
.swiper-wrapper
{

}
.district__achievement-item
{
  width: 400px;
  @include l
  {
    width: 330px;
  }
}
.district__achievement-image
{
  width: 100%;
  height: 285px;
  background-color: #c4c4c4;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 20px;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include l
  {
    height: 200px;
    margin-bottom: 15px;
  }
}
.district__achievement-name
{
  margin-bottom: 10px;
  h3
  {
    @include h4-d;
  }
  @include l
  {
    h3
    {
      @include h4-m;
    }
  }
}
.district__achievement-desc
{
  p
  {
    @include t2-d;
  }
  @include l
  {
    p
    {
      @include t1-m
    }
  }
}
