.custom__radiocheck {
  display: block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  @include t1-d;
}

/* Hide the browser's default checkbox */
.custom__radiocheck input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.custom__radiocheck > span {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0,-50%);
  height: 20px;
  width: 20px;
  border: 3px solid #A43131;
  border-radius: 4px;
}

/* On mouse-over, add a grey background color */
.custom__radiocheck:hover input ~ span {
//  background-color: #ccc;
}
.custom__radiocheck .radio
{
  border-radius: 50%;
}
/* When the checkbox is checked, add a blue background */
.custom__radiocheck input:checked ~ span {
  background-color: #A43131;
}

/* Create the checkmark/indicator (hidden when not checked) */
.custom__radiocheck > span:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.custom__radiocheck input:checked ~ span:after {
  display: block;
}

/* Style the checkmark/indicator */
.custom__radiocheck > span:after {
  left: 5px;
  top: 1px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 2px 2px 0;
  border-radius: 1px;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.page
{
  padding: 0 40px;
  margin-bottom: 150px;
  display: grid;
  grid-template-areas:
  "11 topTabs"
  "leftMenu content";
  // grid-template-rows: 50px 1fr;
 grid-template-columns: auto 1fr;
 @include l
 {
   grid-template-areas:
   "topTabs"
   "leftMenu"
   "content";
   grid-template-columns: 1fr;
   padding: 0;
   margin-bottom: 100px;
 }
  &__left-menu
  {
    grid-area: leftMenu;
  }
  &__top-tabs
  {
    grid-area: topTabs;
  }
  &__content
  {
    grid-area: content;
  }
}
.swiper-css-mode>.swiper-wrapper
{
  overflow-y: hidden ;
}
.df-center
{
  display: flex;
  justify-content: center;
}
.socials-right
{
  display: flex;
  justify-content: center;
  width: 70px;
  padding: 26px 0;
  background-color: #A43131;
  position: fixed;
  right: 0;
  top: 50%;
  transform: translate(0,-50%);
  border-top-left-radius: 5px;
  z-index: 99;
  transition: all .3s ease;
  &_hidden
  {
    right: -70px;
  }
  .icon
  {
    width: 32px;
    height: 32px;
    color: #fff;
  }
  ul
  {
    li
    {
      width: 32px;
      height: 32px;
      margin-bottom: 30px;
      a
      {
        width: 100%;
        height: 100%;
        display: block;
        transition: all .3s ease;
        &:hover
        {
          scale: 1.1;
        }
      }
      &:last-child
      {
        margin-bottom: 0;
      }
    }
  }
  @include xm
  {
    ul
    {
      li
      {
        width: 24px;
        height: 24px;
      }
    }
    width: 50px;
    padding: 20px 0;
    .icon
    {
      width: 24px;
      height: 24px;
      color: #fff;
    }
  }
}
.cookie
{
  position: fixed;
  bottom: 0;
  left: -990px;
  width: 990px;
  background-color: #F1F1F1;
  border-top-right-radius: 5px;
  padding: 20px;
  z-index: 999;
  display: flex;
  align-items: center;
  transition: all .4s ease;
  @include l
  {
    border-radius: 5px;
    bottom: -500px;
    width: 680px;
    left: 50%;
    transform: translate(-50%,0);
  }
  @include xm
  {
    width: 350px;
    flex-direction: column;
  }
  &_active
  {
    left: 0;
    @include l
    {
      bottom: 30px;
      left: 50%;
    }
    @include xm
    {

    }
  }
}
.cookie__text
{
  margin-right: 60px;
  @include t3-d;
  @include xm
  {
    margin-right: 0;
    text-align: center;
    margin-bottom: 20px;
  }
}
.cookie__btn
{
  cursor: pointer;
  padding: 11px 20px;
  background-color: #900000CC;
  color: #fff;
  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  border-radius: 5px;
  white-space: nowrap;
}
