.blog
{
  width: 890px;
  margin: 0 auto;
  margin-bottom: 150px;
  @include l
  {
    width: 100%;
    margin-bottom: 100px;
  }
}
.blog__item
{
  width: 890px;
  margin: 0 auto;
  padding: 0 96px;
  padding-bottom: 66px;
  padding-top: 40px;
  margin-bottom: 50px;
  border-radius: 5px;
  border: 1px solid #f1f1f1;
  &_active
  {
    .blog__item-text
    {
      -webkit-line-clamp: unset;
    }
    .blog__item-desc
    {
      margin-bottom: 0;
    }
  }
  @include l
  {
    width: 100%;
    padding: 20px;
    padding-bottom: 40px;
    margin-bottom: 60px;
  }
  @include xm
  {
    padding-bottom: 30px;
    margin-bottom: 40px;
  }
}
.blog__item-images
{
  width: 100%;
  margin-bottom: 40px;
  position: relative;
  @include xm
  {
    margin-bottom: 30px;
  }
}
.blog__item-image
{
  width: 100%;
  height: 480px;
  border-radius: 5px;
  overflow: hidden;
  img
  {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @include l
  {
    height: 440px;
  }
  @include xm
  {
    height: 250px;
  }
}
.blogSwiper
{
  overflow: hidden;
  @include l
  {
    padding-bottom: 24px;
  }
}
.blogSwiper-pagination
{
  display: none;
  @include l
  {
    display: block;
    height: 6px;
    .swiper-pagination-bullet
    {
      width: 6px;
      height: 6px;
      background: rgba(113, 121, 126, 0.5);
      &-active
      {
        background: #000;
      }
    }
  }
}
.blogSwiper-buttons
{
  position: absolute;
  width: 60px;
  height: 60px;
  top: 50%;
  transform: translate(0,-50%);
  z-index: 9;
  cursor: pointer;
  color: rgb(113, 121, 126);
  transition: all .4s ease;
  .icon
  {
    width: 100%;
    height: 100%;
  }
  &:hover
  {
    color: #aaa;
  }
  @include l
  {
    display: none;
  }
}
.blogSwiper-buttons-next
{
  left: -78px;
}
.blogSwiper-buttons-prev
{
  right: -78px;
  .icon
  {
    transform: rotateZ(180deg);
  }
}
.blog__item-desc
{
  margin-bottom: 60px;
  @include l
  {
    padding: 0 10px;
  }
  @include xm
  {
    padding: 0;
  }
}
.blog__item-date
{
  @include t2-d;
  margin-bottom: 10px;
  @include xm
  {
    @include t1-m
  }
}
.blog__item-name
{
  margin-bottom: 10px;
  h2
  {
    @include h3-d;
    @include xm
    {
      @include h3-m
    }
  }
}
.blog__item-text
{
  margin: 0; /*    */
 -webkit-line-clamp: 5; /*    */
 display: -webkit-box; /*   */
 -webkit-box-orient: vertical; /*   */
 overflow: hidden;
  p
  {
    @include t1-d;
    @include xm
    {
      @include t1-m
    }
  }
}
